<template>
  <div class="blog-comment-container">
    <MessageArea
      title="评论列表"
      :subTitle="`(${data.total})`"
      :list="data.rows"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import MessageArea from '@/components/MessageArea';
import fetchData from '@/mixins/fetchData.js';
import { getComment, postComment } from '@/api/blog.js';
export default {
  mixins: [fetchData({ total: 0, rows: [] })],
  components: {
    MessageArea,
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  created() {
    this.$bus.$on('mainScroll', this.handleScroll);
  },
  destroyed() {
    this.$bus.$off('mainScroll', this.handleScroll);
  },

  computed: {
    hasMore() {
      return this.data.rows.length < this.data.total;
    },
  },

  methods: {
    async fetchData() {
      return await getComment(this.$route.params.id, this.page, this.limit);
    },

    async handleSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(resp); //添加到数组的开头
      this.data.total++;
      callback('评论成功'); // 告诉子组件，我这边处理完了，你继续
    },

    handleScroll(dom) {
      if (this.isLoading || !dom) {
        //正在加载更多 或者 组件已经销毁传递的dom是undefined
        return;
      }
      const rang = 100; //// 顶一个可接受的范围，在这个范围内都算达到了底部
      const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      //dom.scrollTop是dom上方高出的滚动条高度，dom.clientHeight是dom可见区域的高度，dom.scrollHeight是滚动条的总高度
      if (dec <= rang) {
        this.fetchMore();
      }
    },

    //加载下一页
    async fetchMore() {
      if (!this.hasMore) {
        //没有更多了
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
  },
};
</script>

<style>
.blog-comment-container {
  margin: 30px 0;
}
</style>
