<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <RightList :list="tocWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import RightList from './RightList';
import { debounce } from '@/utils';
export default {
  components: {
    RightList,
  },
  props: {
    toc: {
      type: Array,
    },
  },
  data() {
    return {
      activeAnchor: '',
    };
  },

  computed: {
    // 根据toc属性以及activeAnchor得到带有isSelect属性的toc数组
    tocWithSelect() {
      const getTOC = (toc = []) => {
        return toc.map((t) => {
          return {
            ...t,
            isSelect: t.anchor === this.activeAnchor,
            children: getTOC(t.children), //递归
          };
        });
      };
      return getTOC(this.toc);
    },
    //根据toc得到他们对应的元素数组
    doms() {
      const doms = [];
      const addToDoms = (toc) => {
        for (const t of toc) {
          doms.push(document.getElementById(t.anchor));
          if (t.children && t.children.length) {
            addToDoms(t.children); //递归
          }
        }
      };
      addToDoms(this.toc);
      return doms;
    },
  },

  created() {
    this.setSelectDebounce = debounce(this.setSelect, 50); //直接this.也是创建的一种方式
    this.$bus.$on('mainScroll', this.setSelectDebounce);
  },
  destroyed() {
    this.$bus.$off('mainScroll', this.setSelectDebounce);
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
      //跳转锚链接，给我什么锚链接，就去找什么id选择器的位置
    },
    // 设置activeAnchor为正确的值
    setSelect() {
      this.activeAnchor = ''; // 由于后续要重新设置，先清空之前的状态
      const range = 200;
      for (const dom of this.doms) {
        //有可能dom得到是空的
        if (!dom) {
          continue; //下一次循环
        }
        // 得到元素离视口顶部的距离
        const top = dom.getBoundingClientRect().top;
        if (top >= 0 && top <= range) {
          //在规定范围内
          this.activeAnchor = dom.id;
          return;
        } else if (top > range) {
          //在规定范围下方
          return;
        } else {
          //在规定范围上方
          this.activeAnchor = dom.id; // 先假设自己是激活的，然后继续看后面
        }
      }
    },
  },
};
</script>

<style scoped lang="less">
.blog-toc-container {
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>
